
import dataService from "../assets/mixins/dataMixins"

import{ ref, SetupContext, defineComponent } from "vue";
import{ useRoute, useRouter } from "vue-router";

export default defineComponent({
  name: 'EditData',
  setup(props: object,  context: SetupContext<"update:showDataEditionRouterView"[]> ){
    
    const route = useRoute()
    const router = useRouter()

    const ourStudent = ref(Object.assign({}, route.params)).value
    
    delete ourStudent.marks
    delete ourStudent.weights
    delete ourStudent.descriptions
    delete ourStudent.dates

    const showGreenCheckMark = ref({
      firstName: false,
      lastName: false,
      pesel: true,
      street: false,
      phone: false,
      email: false,
      mother: false,
      father: true
    })

    const hideCheckMarkWithLayer = ref({
      firstName: true,
      lastName: true,
      pesel: true,
      street: true,
      phone: true,
      email: true,
      mother: true,
      father: true
    })

    const possibleSave = ref(true);

    function closeThePanel(){

      router.push({name: "Student", params: route.params})
      context.emit("update:showDataEditionRouterView", false);

    }

    return{
      ourStudent,
      showGreenCheckMark,
      hideCheckMarkWithLayer,
      possibleSave,
      closeThePanel,
    ...dataService()
    }

  }
});
