

import { SetupContext } from 'node_modules/vue/dist/vue';
import { defineComponent } from 'vue';

export default defineComponent({
  name: "LoggedOut",
  emits: ['changeLogStatus'],
  setup( props: object,  context: SetupContext<"changeLogStatus"[]>){

    function logMeIn(e: Event){

          e.preventDefault();

          //gets inserted login
          const userLogin = (document.querySelector(".mainLogPanel input[type=text]") as HTMLInputElement).value;

          //gets inserted password
          const userPassword = (document.querySelector(".mainLogPanel input[type=password]") as HTMLInputElement).value;

          //if inserted login and password are correct
          if ((userLogin === "Login1") && (userPassword === "Hasło1")) {


            setTimeout(()=>{
              context.emit('changeLogStatus')
            },800);

          }
            
          //if inserted login or password are incorrect
          else {

              const wrong = document.querySelectorAll(".mainLogPanel span.wrongLoginPassword");

              for (let i = 0; i < wrong.length; i++) {
                  wrong[i].innerHTML = "Login i hasło muszą się zgadzać!"
              }

          }
    }
    
    return{
      logMeIn
    }
  }
})
